const BalanceSection = ({ user }) => (
  <div className="balance-section">
    <NextLessonBoxes user={user} />
    <BalanceBox user={user} />
    <LessonOptions />
    
  </div>
);

const BalanceBox = ({ user }) => (
  <div className="balance-box">
    <div className="balance-wrapper">
      <div className="balance-title">Balance</div>
      <div className="balance-value">
        {/* Display the user balance or relevant info */}
        {user ? ` ${user.balance || 'Not available'}` : 'Loading...'}
      </div>
      <button className="recharge-btn">Recharge</button>
      <div className="crypto-recharge">
        Recharge with crypto <br />(in progress)
      </div>
    </div>
  </div>
);

const LessonOptions = () => (
  <div className="lesson-options">
    <div className="options-list">
      <button className="option-item">Videocall</button>
      <button className="option-item">Workbook</button>
      <button className="option-item">Extra</button>
    </div>
  </div>
);

const NextLessonBoxes = ({ user, nextLesson, nextWorkbook }) => {
  const defaultLessonImage = 'default-lesson.png'; // Use a default image if no lesson image is available
  const defaultWorkbookImage = 'default-workbook.png'; // Use a default image if no workbook image is available

  return (
    <div className="itemMain">
      <div className="detail-item wide">
        <div className="detail-label">YOUR COURSE:</div>
        <h3>{user.courseID || 'No course selected'}</h3>
      </div>

      <div className="next-lesson-box">
        <img 
          className="logo" 
          src={nextLesson?.BGimg || defaultLessonImage} 
          alt="Next Lesson" 
        />
        <div className="next-lesson-text">NEXT LESSON</div>
        {nextLesson ? (
          <div className="lesson-details">
            <h4>{nextLesson.title}</h4>
            <p>{nextLesson.description}</p>
          </div>
        ) : (
          <p>No upcoming lesson</p>
        )}
      </div>

      <div className="next-lesson-box">
        <img 
          className="logo" 
          src={nextWorkbook?.image || defaultWorkbookImage} 
          alt="Next Workbook" 
        />
        <div className="next-lesson-text">NEXT WORKBOOK</div>
        {nextWorkbook ? (
          <div className="workbook-details">
            <h4>{nextWorkbook.title}</h4>
            <p>{nextWorkbook.description}</p>
          </div>
        ) : (
          <p>No upcoming workbook</p>
        )}
      </div>
    </div>
  );
};

export default ProfilePage;
