// src/pages/PricingPage.js

import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import Register from '../authpanel/Register';
import SignIn from '../authpanel/SignIn';
import '../styles/PricingPage.css'; // Custom styles for pricing

import diamond from '../assets/prices/diamond-yellow.svg';
import crown from '../assets/prices/crown.svg';
import touch from '../assets/prices/touch.svg';
import limit from '../assets/prices/part.svg';
import solo from '../assets/prices/solo.svg';
import logic from '../assets/prices/logic.svg';
import topic from '../assets/prices/topic.svg';
import grammar from '../assets/prices/grammar.svg';
import speaking from '../assets/prices/speaking.svg';
import reading from '../assets/prices/reading.svg';
import vocabulary from '../assets/prices/vocabulary.svg';
import writing from '../assets/prices/writing.svg';
import listening from '../assets/prices/listening.svg';
import caution from '../assets/prices/caution.svg';

/* global Wayforpay */ // Notify ESLint that Wayforpay is a global variable

// Define tiers outside the component to prevent recreation on every render
const tiers = [
  { id: 'A0.Beginner', lessonPrice: 5, modulePrice: 20, bundlePrice: 70, moduleSub: "6 lessons", stayerSub: "4 modules" },
  { id: 'A1.Elementary', lessonPrice: 6, modulePrice: 25, bundlePrice: 85, moduleSub: "6 lessons", stayerSub: "4 modules" },
  { id: 'A2.Pre-intermediate', lessonPrice: 8, modulePrice: 30, bundlePrice: 100, moduleSub: "6 lessons", stayerSub: "4 modules" },
  { id: 'B1.Intermediate', lessonPrice: 10, modulePrice: 35, bundlePrice: 120, moduleSub: "5 lessons", stayerSub: "4 modules" },
  { id: 'B1+.Advanced intermediate', lessonPrice: 12, modulePrice: 40, bundlePrice: 140, moduleSub: "5 lessons", stayerSub: "4 modules" },
  { id: 'B2.Upper-intermediate', lessonPrice: 15, modulePrice: 50, bundlePrice: 160, moduleSub: "7 lessons", stayerSub: "3 modules" },
  { id: 'C1.Advanced', lessonPrice: 18, modulePrice: 60, bundlePrice: 200, moduleSub: "7 lessons", stayerSub: "3 modules" },
];

const PricingPage = () => {
  const { user } = useAuth();
  const [overlayType, setOverlayType] = useState(null); // null, 'register', 'login'
  const [currentTier, setCurrentTier] = useState(null);
  const [userTier, setUserTier] = useState(null);
  const [paymentStatus, setPaymentStatus] = useState(null); // null, 'success', 'failed', 'processing'

  // Set the current tier based on user information
  useEffect(() => {
    if (user && user.courseID) {
      const tier = tiers.find(t => t.id.startsWith(user.courseID));
      if (tier) {
        setCurrentTier(tier);
        setUserTier(tier); // Set the user's subscribed tier
      }
    }
    // No need to include 'tiers' in dependencies as it's a stable reference
  }, [user]);

  // Dynamic loading of the WayForPay widget script
  useEffect(() => {
    const scriptId = 'widget-wfp-script';
    if (!document.getElementById(scriptId)) {
      const script = document.createElement('script');
      script.src = 'https://secure.wayforpay.com/server/pay-widget.js';
      script.id = scriptId;
      script.async = true;
      script.onload = () => console.log("Wayforpay script loaded");
      document.body.appendChild(script);
    }
  }, []);

  // Function to change the selected tier
  const handleTierChange = (tier) => {
    setCurrentTier(tier);
  };

  // Function to create the payment request body
  const createPaymentBody = (tier, type) => {
    let amount = 0;
    let productName = '';

    switch (type) {
      case 'lesson':
        amount = tier.lessonPrice;
        productName = 'Lesson';
        break;
      case 'module':
        amount = tier.modulePrice;
        productName = 'Module';
        break;
      case 'bundle':
        amount = tier.bundlePrice;
        productName = 'Bundle';
        break;
      default:
        throw new Error('Invalid payment type');
    }

    return {
      merchantAccount: 'anglomova_com', // Replace with your actual merchant account
      orderReference: `${user.uid}-${Date.now()}`, // Unique order reference
      amount: "10.00",
      currency: 'USD',
      merchantDomainName: 'https://www.anglomova.com', // Replace with your actual domain
      productNames: ["Lesson"],
      productCounts: [1],
      productPrices: ["10.00"],
      // Add other necessary fields if required by WayForPay
    };
  };

  // Function to get the merchant signature from the backend using fetch
  const getSignature = async (paymentBody) => {
    try {
      const response = await fetch('https://us-central1-anglomova-429e2.cloudfunctions.net/wayforpaySignature', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(paymentBody)
      });

      if (!response.ok) {
        const errorText = await response.text();
        throw new Error(`Server responded with status ${response.status}: ${errorText}`);
      }

      const data = await response.json();
      return data.signature;
    } catch (error) {
      console.error('Error generating signature:', error);
      return null;
    }
  };

  // Function to handle successful payment
  const handlePaymentSuccess = () => {
    setPaymentStatus('success');
    alert('Payment was successful!');
    window.location.reload(); // Reload the page after successful payment
  };

  // Function to handle failed payment
  const handlePaymentFailure = (response) => {
    setPaymentStatus('failed');
    alert(`Payment failed: ${response.reason || 'Unknown error'}`);
    console.error('Payment failed', response);
  };

  // Function to handle payment in progress
  const handlePaymentInProgress = (response) => {
    setPaymentStatus('processing');
    console.log('Payment in progress', response);
  };

  // Main payment handling function
  const handlePayment = async (tier, type) => {
    if (typeof Wayforpay === 'undefined') {
      console.error('Wayforpay is not available');
      alert('Payment service is currently unavailable. Please try again later.');
      return;
    }

    const paymentBody = createPaymentBody(tier, type);
    const signature = await getSignature(paymentBody);

    if (!signature) {
      console.error('Failed to get signature');
      alert('Payment could not be processed at this time. Please try again later.');
      return;
    }

    // Construct the payment object as per WayForPay requirements
    const paymentData = {
      merchantAccount: paymentBody.merchantAccount,
      merchantDomainName: paymentBody.merchantDomainName,
      orderReference: paymentBody.orderReference,
      orderDate: Math.floor(Date.now() / 1000),
      amount: paymentBody.amount,
      currency: paymentBody.currency,
      productName: paymentBody.productNames,
      productPrice: paymentBody.productPrices,
      productCount: paymentBody.productCounts,
      clientFirstName: user.firstName || 'Unknown',
      clientLastName: user.lastName || 'Unknown',
      clientEmail: user.email || 'unknown@mail.com',
      clientPhone: user.phone || '380631234567',
      authorizationType: 'SimpleSignature',
      merchantSignature: signature,
    };

    // Initialize WayForPay and run the widget
    try {
      const wayforpay = new Wayforpay();
      wayforpay.run(paymentData,
        handlePaymentSuccess,
        handlePaymentFailure,
        handlePaymentInProgress
      );
      setPaymentStatus('processing');
    } catch (error) {
      console.error('Error initializing WayForPay widget:', error);
      alert('An error occurred while processing the payment.');
    }
  };

  // Refactored Overlay component
  const Overlay = ({ title, onClose, children }) => (
    <div className="overlay">
      <div className="overlay-content reg">
        <h2>{title}</h2>
        <div className="close-btn" onClick={onClose}></div>
        {children}
      </div>
    </div>
  );

  return (
    <div className="background">
      {overlayType === 'register' && (
        <Overlay title="REGISTER" onClose={() => setOverlayType(null)}>
          <Register onClose={() => setOverlayType(null)} />
          <p>
            Already have an account?{' '}
            <span className='changeOver' onClick={() => setOverlayType('login')}>
              SIGN IN
            </span>
          </p>
        </Overlay>
      )}
      {overlayType === 'login' && (
        <Overlay title="LOG IN" onClose={() => setOverlayType(null)}>
          <SignIn onClose={() => setOverlayType(null)} />
          <p>
            Don't have an account?{' '}
            <span className='changeOver' onClick={() => setOverlayType('register')}>
              REGISTER
            </span>
          </p>
        </Overlay>
      )}

      <div className="container-price">
        {user && userTier && (
          <div className="user-subscription">
            <div className='info'> Your Subscription Tier:</div>
            <h2 className="subscription-header"> {user.courseID}</h2>
            <div className="panel pricing-table">
              <PricingPlan tier={userTier} handlePayment={handlePayment} /> {/* Pass handlePayment as a prop */}
            </div>
            <h2 className="subscription-header gap">Compare to other courses:</h2>
          </div>
        )}

        <div className="radio-inputs">
          {tiers.map(tier => {
            const [level, description] = tier.id.split('.');
            return (
              <div className="radio" key={tier.id}>
                <input
                  type="radio"
                  id={`tier-${tier.id}`}
                  name="tier"
                  checked={tier.id === currentTier?.id}
                  onChange={() => handleTierChange(tier)}
                />
                <label
                  htmlFor={`tier-${tier.id}`}
                  className={`name ${tier.id === currentTier?.id ? 'active' : ''}`}
                >
                  <div className='detItem'>{description.trim()}</div>
                  {level}
                </label>
              </div>
            );
          })}

          {currentTier && (
            <div className="panel pricing-table">
              <button
                className="pricing-button"
                onClick={() => handlePayment(currentTier, 'lesson')}
              >
                Buy Lesson - ${currentTier.lessonPrice}
              </button>
              <button
                className="pricing-button"
                onClick={() => handlePayment(currentTier, 'module')}
              >
                Buy Module - ${currentTier.modulePrice}
              </button>
              <button
                className="pricing-button"
                onClick={() => handlePayment(currentTier, 'bundle')}
              >
                Buy Bundle - ${currentTier.bundlePrice}
              </button>
            </div>
          )}
        </div>

        {/* Display payment status messages */}
        {paymentStatus === 'processing' && (
          <div className="payment-status processing">
            <p>Processing your payment...</p>
          </div>
        )}
        {paymentStatus === 'success' && (
          <div className="payment-status success">
            <p>Payment was successful! Thank you.</p>
          </div>
        )}
        {paymentStatus === 'failed' && (
          <div className="payment-status failed">
            <p>Payment failed. Please try again.</p>
          </div>
        )}
      </div>
    </div>
  );
};

// Component to display individual pricing plans
const PricingPlan = ({ tier, handlePayment }) => {
  return (
    <>
      <div className="pricing-category">
        <h2 className="category-header">Single</h2>
        <div className="pricing-plan starter">
          <h3 className="pricing-header">1 Lesson + 1 Workbook</h3>
          <ul className="pricing-description">
            <li><img src={touch} alt="touch" /><span>Interactive tasks</span></li>
            <li><img src={limit} alt="limit" /><span>Limited insights into module's topic</span></li>
            <li><img src={logic} alt="logic" /><span>Logically-structured tasks</span></li>
            <li><img src={solo} alt="solo" /><span>You learn how to learn without a teacher</span></li>
          </ul>

          <span className="pricing-price">${tier.lessonPrice}</span>
          <button onClick={() => handlePayment(tier, 'lesson')} className="pricing-button">Buy Lesson</button>
        </div>
      </div>

      <div className="pricing-category">
        <div className="category-header"><img src={diamond} alt="diamond" /> Module</div>
        <div className="pricing-plan greener">
          <h3 className="pricing-header">{tier.moduleSub}</h3>
          <ul className="pricing-description">
            <li><img src={topic} alt="topic" /><span>~5 connected topics</span></li>
            <li><img src={grammar} alt="grammar" /><span>~3 grammar bundles</span></li>
            <li><img src={vocabulary} alt="vocabulary" /><span>Ultimate vocabulary boost</span></li>
            <li><img src={writing} alt="writing" /><span>Secrets of writing like a Lord</span></li>
            <li><img src={speaking} alt="speaking" /><span>Solid speaking section</span></li>
            <li><img src={reading} alt="reading" /><span>Reading section + techniques</span></li>
            <li><img src={listening} alt="listening" /><span>Listening [in development...]</span></li>
          </ul>
          <span className="pricing-price">${tier.modulePrice}</span>
          <button onClick={() => handlePayment(tier, 'module')} className="pricing-button">Buy Module</button>
        </div>
      </div>

      <div className="pricing-category">
        <div className="category-header"><img src={crown} alt="crown" /> Stayer</div>
        <div className="pricing-plan purpler">
          <h3 className="pricing-header">{tier.stayerSub}</h3>
          <p className="pricing-description notify">
            <div className='caution'><img src={caution} alt="caution" height="28px" /> <span>Caution!</span></div>
            This bundle contains half of the course. Extended learning for persistent learners.
          </p>
          <span className="pricing-price">${tier.bundlePrice}</span>
          <button onClick={() => handlePayment(tier, 'bundle')} className="pricing-button">Buy Bundle</button>
        </div>
      </div>
    </>
  );
};

export default PricingPage;
