import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import DOMPurify from 'dompurify';
import '../../styles/CurrentLesson.css';
import '../../styles/LessonPage.css';
import { handleInteraction, PART_KEYS } from './utils';
import { useLearning } from '../../contexts/LearningContext';
import leftIcon from '../../assets/left.svg';
import rightIcon from '../../assets/right.svg';


const LessonPage = () => {
  const { lessonID } = useParams();
  const { selectedCourse, finishLesson, userInteractions, updateUserInteraction } = useLearning();
  const [lesson, setLesson] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [finishError, setFinishError] = useState(null);
  const [currentPartIndex, setCurrentPartIndex] = useState(0);
  const [currentPartName, setCurrentPartName] = useState('');
  const [startTime, setStartTime] = useState(null);
  const [correctAnswers, setCorrectAnswers] = useState(0);
  const [totalQuestions, setTotalQuestions] = useState(0);
  const navigate = useNavigate();

  const getFlattenedLessons = (course) => {
    if (!course || !Array.isArray(course.modules)) return [];
    return course.modules.flatMap((module) => module.lessons || []);
  };

  const loadLesson = (lessons, targetLessonID) => {
    const lessonData = lessons.find((lesson) => lesson.id === targetLessonID) || lessons.find(lesson => !lesson.completed) || lessons[0];
    if (lessonData) {
      setLesson(lessonData);
      calculateTotalQuestions(lessonData);
      updateCurrentPartName(lessonData, 0);
      setStartTime(Date.now());
      setLoading(false);
    } else {
      setError('Lesson not found');
      setLoading(false);
    }
  };

  useEffect(() => {
    if (selectedCourse) {
      const flattenedLessons = getFlattenedLessons(selectedCourse);
      loadLesson(flattenedLessons, lessonID);
    } else {
      setError('Selected course or modules data not available');
      setLoading(false);
    }
  }, [selectedCourse, lessonID]);

  useEffect(() => {
    if (lesson) {
      const cleanup = handleInteraction();
      updateCurrentPartName(lesson, currentPartIndex);
      return cleanup;
    }
  }, [lesson, currentPartIndex]);

  const calculateTotalQuestions = (lesson) => {
    const total = PART_KEYS.filter((key) => key.includes('input') && lesson[key]).length;
    setTotalQuestions(total);
  };

  const updateCurrentPartName = (lesson, index) => {
    const partKey = `Part${index + 1}/Type`;
    const partName = lesson[partKey] || `Part ${index + 1}`;
    setCurrentPartName(partName);
  };

  const handleNext = () => {
    let newIndex = currentPartIndex + 1;
    if (newIndex <= 5) {
      setCurrentPartIndex(newIndex);
    }
  };

  const handlePrevious = () => {
    let newIndex = currentPartIndex - 1;
    if (newIndex >= 0) {
      setCurrentPartIndex(newIndex);
    }
  };

  const handleFinishLesson = async () => {
    try {
      const endTime = Date.now();
      const timeSpent = Math.round((endTime - startTime) / 1000);

      const lessonStats = {
        timeSpent,
        correctAnswers,
        totalQuestions,
      };

      await finishLesson(lessonID, lessonStats);

      const flattenedLessons = getFlattenedLessons(selectedCourse);
      const nextLesson = flattenedLessons[0];

      if (nextLesson) {
        navigate(`/course/${selectedCourse.id}/module/${nextLesson.moduleID}/lesson/${nextLesson.id}`);
      } else {
        navigate('/courses');
      }
    } catch (error) {
      setFinishError('Failed to finish the lesson. Please try again.');
    }
  };


  if (error) {
    return <div>{error}</div>;
  }

  if (!lesson) {
    return <div>Lesson data is missing</div>;
  }

  return (
    <div>
      <LessonHeader currentPartName={currentPartName} lesson={lesson} />
      <div className='lesson-page'>
      <LessonNavigationL
        currentPartIndex={currentPartIndex}
        handlePrevious={handlePrevious}
      />
      <RenderLessonParts
        currentPartIndex={currentPartIndex}
        lesson={lesson}
        userInteractions={userInteractions}
      />
      <LessonNavigationR
        currentPartIndex={currentPartIndex}
        handleNext={handleNext}
      />
      </div>
      {currentPartIndex === 5 && (
        <LessonSummary
          startTime={startTime}
          correctAnswers={correctAnswers}
          totalQuestions={totalQuestions}
          handleFinishLesson={handleFinishLesson}
        />
      )}
      {finishError && <div className="error">{finishError}</div>}
      <ProgressBar currentPartIndex={currentPartIndex} totalParts={6} />
    </div>
  );
};

const LessonHeader = ({ currentPartName, lesson }) => (
  <div className="lp-top yel-red">
    <div className="lp-top-crsmod">
      <div className="uppercase">{lesson.ModuleID}</div>
      <div>{lesson.lessonName}</div>
    </div>
    <div className="wide">
      <h1>{currentPartName}</h1>
    </div>
  </div>
);

const LessonNavigationL = ({ currentPartIndex, handlePrevious }) => (
  <div>
    <div
      className={`navigationL ${currentPartIndex === 0 ? 'disabled' : ''}`}
      onClick={currentPartIndex === 0 ? null : handlePrevious}
    >
      <img src={leftIcon} alt="left" className="navIcon-pos" />
    </div>
    
  </div>
);
const LessonNavigationR = ({ currentPartIndex, handleNext }) => (
  <div>
    <div
      className={`navigationR ${currentPartIndex === 5 ? 'disabled' : ''}`}
      onClick={currentPartIndex === 5 ? null : handleNext}
    >
      <img src={rightIcon} alt="right" className="navIcon-pos" />
    </div>
  </div>
);

const RenderLessonParts = ({ currentPartIndex, lesson, userInteractions }) => {
  const currentPartPrefix = `Part${currentPartIndex + 1}`;

  // Define the correct order for the lesson keys
  const partOrder = [
    `${currentPartPrefix}/A.Warm-up`,
    `${currentPartPrefix}/A.task`,
    `${currentPartPrefix}/A.kit`,
    `${currentPartPrefix}/A.answer`,
    `${currentPartPrefix}/A.pic`,
    `${currentPartPrefix}/A.article`,
    `${currentPartPrefix}/A.articleAnswers`,
    `${currentPartPrefix}/A.audio`,
    `${currentPartPrefix}/A.hint`,
    `${currentPartPrefix}/A.transcription`,
    `${currentPartPrefix}/B.task`,
    `${currentPartPrefix}/B.kit`,
    `${currentPartPrefix}/B.answer`,
    `${currentPartPrefix}/C.task`,
    `${currentPartPrefix}/C.kit`,
    `${currentPartPrefix}/C.answers`,
    `${currentPartPrefix}/D.task`,
    `${currentPartPrefix}/D.kit`,
    `${currentPartPrefix}/D.answer`,
  ];

  // Filter the part keys based on what exists in the lesson and match the correct order
  const currentPartKeys = partOrder.filter((key) => lesson[key]);

  return (
    <div className="mane">
      {currentPartKeys.length > 0 ? (
        currentPartKeys.map((key, index) => {
          const content = lesson[key] ? DOMPurify.sanitize(lesson[key]) : null;

          if (!content) return <div key={index}>Content is missing for this part.</div>;

          const interaction = userInteractions?.[key] || { value: '', correct: false };

          // Render based on key type
          if (key.includes('input')) {
            return (
              <input
                key={index}
                type="text"
                data-correct={lesson[key]?.toLowerCase() || ''}
                name={key}
                value={interaction.value || ''}
                className={interaction.correct ? 'correct' : interaction.value ? 'incorrect' : ''}
                autoComplete="new-password"
                autoCorrect="off"
                autoCapitalize="none"
                spellCheck="false"
              />
            );
          } else if (key.includes('.Warm-up')) {
            return <div className="warming" key={index} dangerouslySetInnerHTML={{ __html: content }} />;
          } else if (key.includes('.task')) {
            return <h3 key={index} dangerouslySetInnerHTML={{ __html: content }} />;
          } else if (key.includes('.hint')) {
            return <div className="hint" key={index} dangerouslySetInnerHTML={{ __html: content }} />;
          } else {
            return <div key={index} className="part" dangerouslySetInnerHTML={{ __html: content }} />;
          }
        })
      ) : (
        <div>No content available for this part</div>
      )}
    </div>
  );
};



const LessonSummary = ({ startTime, correctAnswers, totalQuestions, handleFinishLesson }) => (
  <div className="endpoint">
    <h2>Lesson Summary</h2>
    <p>Time Spent: {Math.round((Date.now() - startTime) / 1000)} seconds</p>
    <p>Correct Answers: {correctAnswers} / {totalQuestions}</p>
    <button onClick={handleFinishLesson}>Finish Lesson</button>
  </div>
);

const ProgressBar = ({ currentPartIndex, totalParts }) => (
  <div className="progress-bar">
    {[...Array(totalParts).keys()].map((i) => (
      <div key={i} className={`progress-step ${i === currentPartIndex ? 'active' : ''}`} />
    ))}
  </div>
);

export default LessonPage;
