import { kebabCase } from 'lodash';

// Part keys for handling lesson parts
export const PART_KEYS = [
  'Part1/Type', 'Part1/A.Warm-up', 'Part1/A.task', 'Part1/A.kit', 'Part1/A.pic', 'Part1/A.article', 'Part1/A.audio', 'Part1/A.hint',
  'Part1/B.task', 'Part1/B.kit', 'Part1/C.task', 'Part1/C.kit', 'Part1/D.task', 'Part1/D.kit',
  'Part2/Type', 'Part2/A.task', 'Part2/A.kit', 'Part2/A.pic', 'Part2/A.article', 'Part2/A.audio', 'Part2/A.hint',
  'Part2/B.task', 'Part2/B.kit', 'Part2/C.task', 'Part2/C.kit', 'Part2/D.task', 'Part2/D.kit',
  'Part3/Type', 'Part3/A.task', 'Part3/A.kit', 'Part3/A.pic', 'Part3/A.article', 'Part3/A.audio', 'Part3/A.hint',
  'Part3/B.task', 'Part3/B.kit', 'Part3/C.task', 'Part3/C.kit', 'Part3/D.task', 'Part3/D.kit',
  'Part4/Type', 'Part4/A.task', 'Part4/A.kit', 'Part4/A.pic', 'Part4/A.article', 'Part4/A.audio', 'Part4/A.hint',
  'Part4/B.task', 'Part4/B.kit', 'Part4/C.task', 'Part4/C.kit', 'Part4/D.task', 'Part4/D.kit',
  'Part5/Type', 'Part5/A.task', 'Part5/A.kit', 'Part5/A.pic', 'Part5/A.article', 'Part5/A.audio', 'Part5/A.hint',
  'Part5/B.task', 'Part5/B.kit', 'Part5/C.task', 'Part5/C.kit', 'Part5/D.task', 'Part5/D.kit'
];

// Utility function to find the correct parent with a `data-correct` attribute
const findCorrectParent = (element) => {
  while (element) {
    if (element.hasAttribute('data-correct')) {
      return element;
    }
    element = element.parentElement;
  }
  return null;
};

// Event handler for text inputs
const handleTextInputChange = (e) => {
  const inputElement = e.target;
  const correctValue = inputElement.getAttribute('data-correct');
  const selectedValue = inputElement.value.trim();

  const kebabCorrectValue = kebabCase(correctValue);
  const kebabSelectedValue = kebabCase(selectedValue);

  // Toggle correct/incorrect class based on comparison
  inputElement.classList.toggle('correct', kebabSelectedValue === kebabCorrectValue);
  inputElement.classList.toggle('incorrect', kebabSelectedValue !== kebabCorrectValue);
  inputElement.style.backgroundColor = kebabSelectedValue === kebabCorrectValue ? 'lightgreen' : 'lightcoral';
};

// Event handler for select and radio elements
const handleSelectChange = (e) => {
  const target = e.target;
  const correctValue = target.getAttribute('data-correct') || findCorrectParent(target)?.getAttribute('data-correct');
  const selectedValue = target.value;

  const correctParent = findCorrectParent(target) || target;
  const isCorrect = selectedValue === correctValue;

  // Toggle correct/incorrect class based on selection
  correctParent.classList.toggle('correct', isCorrect);
  correctParent.classList.toggle('incorrect', !isCorrect);
  correctParent.style.backgroundColor = isCorrect ? 'lightgreen' : 'lightcoral';
};

// Event handler for button clicks
const handleButtonClick = (e) => {
  if (e.target.tagName === 'BUTTON') {
    const correctParent = findCorrectParent(e.target);
    if (correctParent) {
      const correctValue = correctParent.getAttribute('data-correct');
      const clickedValue = e.target.id;

      // Toggle correct/incorrect class based on button click
      e.target.classList.toggle('correct', clickedValue === correctValue);
      e.target.classList.toggle('incorrect', clickedValue !== correctValue);
    }
  }
};

// Function to add event listeners to interactive elements
export const addEventListeners = () => {
  document.querySelectorAll('input[type="text"][data-correct]').forEach(input => {
    input.addEventListener('input', handleTextInputChange);
  });

  document.querySelectorAll('input[type="radio"], select').forEach(element => {
    element.addEventListener('change', handleSelectChange);
  });

  document.addEventListener('click', handleButtonClick);
};

// Function to remove event listeners from interactive elements
export const removeEventListeners = () => {
  document.querySelectorAll('input[type="text"][data-correct]').forEach(input => {
    input.removeEventListener('input', handleTextInputChange);
  });

  document.querySelectorAll('input[type="radio"], select').forEach(element => {
    element.removeEventListener('change', handleSelectChange);
  });

  document.removeEventListener('click', handleButtonClick);
};

// Core interaction handler to initialize listeners and cleanup
export const handleInteraction = () => {
  addEventListeners(); // Add event listeners

  // Return cleanup function to remove listeners
  return () => {
    removeEventListeners();
  };
};
